"use strict";


// Burger menu
const navMenu = document.querySelector('#nav-menu'),
toggleMenu = document.querySelector('#toggle-menu'),
closeMenu = document.querySelector('#close-menu')
if(toggleMenu){
toggleMenu.addEventListener('click', () => {
    if(toggleMenu){
        navMenu.classList.toggle('show')
    }
})
closeMenu.addEventListener('click', () => {
    if(closeMenu){
        navMenu.classList.remove('show')
    }
})
}


//filtre
const elements = document.querySelectorAll('.no-local');
var toggleButton = document.querySelector("input");
if(toggleButton){
toggleButton.addEventListener("click", toggleCallback);

    for (const element of elements) {
        element.style.display = 'list-item';
      }
    toggleButton.checked = false;
}
function toggleCallback(){
    if(document.body.classList.toggle("dark")){
        for (const element of elements) {
            element.style.display = 'none';
          }
    }else{
          for (const element of elements) {
            element.style.display = 'list-item';
          }
    }
}
//recherche
function search() {
    let input = document.querySelector('#searchbar').value
    input=input.toLowerCase();
    let x = document.querySelectorAll('.beer__bar');
    console.log(x);
    let i  
    for (i = 0; i < x.length; i++) { 
        if (!x[i].innerHTML.toLowerCase().includes(input)) {
            x[i].style.display="none";
        }
        else {
            x[i].style.display="list-item";                 
        }
    }
}

//afficher map/list
let togg1 = document.querySelector("#togg1");
let togg2 = document.querySelector("#togg2");
let d1 = document.querySelector("#d1");
let d2 = document.querySelector("#d2");
if(togg1){
    togg1.addEventListener("click", () => {
    
        if(getComputedStyle(d1).display != "none"){
            d1.style.display = "block";
            d2.style.display = "none";
            togg1.classList.add("border");
            togg2.classList.remove("border");
          } 
          else {
            d1.style.display = "block";
            d2.style.display = "none";
            togg1.classList.add("border");
            togg2.classList.remove("border");
          } 
    
  
    })
}
if(togg1){
    togg2.addEventListener("click", () => {
        if(getComputedStyle(d2).display != "none"){
            d2.style.display = "block";
            d1.style.display = "none";
            togg2.classList.add("border");
            togg1.classList.remove("border");
        } 
        else {
            d2.style.display = "block";
            d1.style.display = "none";
            togg2.classList.add("border");
            togg1.classList.remove("border");
        }
    })
}
