//back to top btn
window.onscroll = function () {
    const backToTop = document.querySelector(".back-to-top");
    if (backToTop){
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        backToTop.style.display = "flex";
      } else {
        backToTop.style.display = "none";
      }
    }
    
}

var bouton1 = document.querySelector('#one')
var bouton2 = document.querySelector('#two')
var body = document.querySelector('body')
var modal_container1 = document.querySelector('#modal-container1')
var modal_container2 = document.querySelector('#modal-container2')

if(modal_container1){
bouton1.addEventListener("click",function onClick() {
  body.classList.add('modal-active')
  modal_container1.removeAttribute('class')
  modal_container1.classList.add('modal-container')
  modal_container1.classList.add('two')
})
modal_container1.addEventListener("click",function onClick() {
  this.classList.add('out')
  body.classList.remove('modal-active')
  
})
bouton2.addEventListener("click",function onClick() {
  body.classList.add('modal-active')
  modal_container2.removeAttribute('class')
  modal_container2.classList.add('modal-container')
  modal_container2.classList.add('two')
})
modal_container2.addEventListener("click",function onClick() {
  this.classList.add('out')
  body.classList.remove('modal-active')
  
})
}
